.burger {
  display: inline-block;
  width: 50px;
  height: 6px;
  border-radius: 3px;
  background-color: #fff;
  margin: 12px 0;
  position: relative;
}
.burger:before {
  content: '';
  display: inline-block;
  width: 50px;
  height: 6px;
  border-radius: 3px;
  background-color: #fff;
  position: absolute;
  top: -12px;
  left: 0;
}
.burger:after {
  content: '';
  display: inline-block;
  width: 50px;
  height: 6px;
  border-radius: 3px;
  background-color: #fff;
  position: absolute;
  bottom: -12px;
  left: 0;
}
.burger__wrapper {
  padding: 10px;
  display: none;
  cursor: pointer;
}
